html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "Gotham Book"; src: url("fonts/Gotham-Book.eot"); src: url("fonts/Gotham-Book.eot?#iefix") format("embedded-opentype"), url("fonts/Gotham-Book.woff2") format("woff2"), url("fonts/Gotham-Book.woff") format("woff"), url("fonts/Gotham-Book.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Gotham Light"; src: url("fonts/Gotham-Light.eot"); src: url("fonts/Gotham-Light.eot?#iefix") format("embedded-opentype"), url("fonts/Gotham-Light.woff2") format("woff2"), url("fonts/Gotham-Light.woff") format("woff"), url("fonts/Gotham-Light.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Gotham-Medium"; src: url("fonts/Gotham-Medium.eot"); src: url("fonts/Gotham-Medium.eot?#iefix") format("embedded-opentype"), url("fonts/Gotham-Medium.woff2") format("woff2"), url("fonts/Gotham-Medium.woff") format("woff"), url("fonts/Gotham-Medium.ttf") format("truetype"); font-weight: normal; font-style: normal; }
h2 { font-size: 48px; }

h3 { font-size: 26px; }

h5 { font-size: 16px; }

h6 { font-size: 13px; }

body { font-family: "Gotham Light", Arial, sans-serif; font-size: 14px; line-height: 1.4; padding-top: 78px; color: #526f78; min-height: 100%; }

.container { width: 100%; min-width: 320px; max-width: 1210px; margin: 0 auto; padding: 0 20px; box-sizing: border-box; }

.contact { background-color: #eaedee; padding-top: 72px; padding-bottom: 82px; }
.contact__wrap { display: flex; flex-direction: row; justify-content: space-around; }

a { text-decoration: none; color: #ccc; }

.logo { padding: 16px 0; }

.actions__list { display: flex; flex-direction: row; align-items: center; }
.actions__el { margin-left: 15px; }
.actions__link { color: #75858f; text-transform: uppercase; font-size: 14px; }

.about__list { display: flex; flex-direction: row; align-items: flex-end; }
.about__el { margin-right: 16px; }
.about__link { font-family: "Gotham-Medium", Arial, sans-serif; font-size: 16px; }
.about__link:hover { text-decoration: underline; }

.ico { display: inline-block; }
.ico_in { background-image: url("../img/ico-in.png"); width: 22px; height: 22px; }
.ico_fb { background-image: url("../img/ico-fb.png"); width: 22px; height: 22px; }

.copyright__text { margin-right: 15px; }
.copyright__link { font-family: "Gotham-Medium", Arial, sans-serif; }
.copyright__link:hover { text-decoration: underline; }

.col { width: 250px; }
.col a { font-family: "Gotham-Medium", Arial, sans-serif; color: #07cfd6; }
.col h6, .col h5 { color: #2f3641; text-transform: uppercase; }
.col h5 { margin-bottom: 10px; }
.col h6 { margin-bottom: 5px; }

.header { width: 100%; background: url("../img/header-bg.png") top center no-repeat; background-size: cover; }
.header h1 { font-weight: 100; font-size: 62px; font-family: "Gotham Light", Arial, sans-serif; padding: 234px 0; color: #fff; }

.info { padding: 100px 0; background-color: #fff; }
.info__strong { font-size: 18px; font-family: "Gotham Book", Arial, sans-serif; color: #2f3641; margin-bottom: 20px; }
.info__descr { font-size: 16px; color: #526f78; }
.info__wrap { display: flex; flex-direction: row; flex-wrap: wrap; }
.info__col { margin-bottom: 10px; width: 50%; }
.info__col_right { text-align: right; }
.info__col_center { text-align: center; }
.info__title { font-size: 26px; text-transform: uppercase; color: #2f3641; }

.figure-group { display: flex; justify-content: space-between; }

.figure { text-transform: uppercase; }
.figure__strong { color: #2f3641; font-family: "Gotham-Medium", Arial, sans-serif; }
.figure__ico { filter: grayscale(100%); }

.footer { padding-top: 50px; padding-bottom: 89px; width: 100%; background-color: #2f3641; }
.footer__top { text-align: center; margin-bottom: 48px; }
.footer__bottom { display: flex; flex-direction: row; justify-content: space-between; }

.main-menu { width: 100%; height: 78px; position: fixed; top: 0; left: 0; z-index: 2; background-color: #fff; }
.main-menu__wrap { display: flex; flex-direction: row; justify-content: space-between; align-items: center; }

.menu { height: 78px; display: flex; flex-direction: row; }
.menu__list { display: flex; flex-direction: row; }
.menu__el { box-sizing: border-box; }
.menu__link { display: block; padding: 0 17px; line-height: 78px; text-align: center; color: #75858f; text-transform: uppercase; font-size: 14px; transition: color 0.3s; box-sizing: border-box; }
.menu__link:hover { font-family: "Gotham Book", Arial, sans-serif; color: #526f78; }

.btn { display: inline-block; position: relative; text-transform: uppercase; font-size: 14px; border-radius: 16px; cursor: pointer; }
.btn:hover { font-family: "Gotham Book", Arial, sans-serif; color: #526f78; }
.btn_blue { font-size: 13px; color: #fff; height: 32px; line-height: 32px; padding: 0 15px; background-color: #07cfd6; }
.btn_blue:hover { font-family: "Gotham Light", Arial, sans-serif; color: #fff; }
.btn_big { width: 363px; font-family: "Gotham-Medium", Arial, sans-serif; font-size: 20px; height: 69px; line-height: 69px; border-radius: 34px; transition: background 0.3s; }
.btn_big:hover { font-family: "Gotham-Medium", Arial, sans-serif; background-color: #079fd6; }


